import * as React from "react";
import Box from "@mui/material/Box";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
// import Select from "@mui/material/Select";
import $ from "jquery";
import Select, { components } from "react-select";
export default function BasicSelect({ options, value, handleChange }) {
  const GREY50 = "#F9FAFB";
  const GREY100 = "#F2F4F7";
  const GREY300 = "#D0D5DD";
  const GREY400 = "#98A2B3";
  const GREY700 = "#344054";
  const GREY900 = "#101828";
  const ShadowLG =
    "0px 12px 16px -4px rgba(16, 24, 40, 0.08), 0px 4px 6px -2px rgba(16, 24, 40, 0.03)";
  // console.log(options);
  const Styles = {
    menu: (styles) => ({
      ...styles,
      marginTop: 5,
      padding: "4px 0",
      hyphens: "auto",
      boxShadow: ShadowLG,
      borderRadius: 8,
      border: `1px solid ${GREY100}`,
      overflow: "hidden",
    }),
    input: (styles) => ({
      ...styles,
      padding: "0",
      margin: "0",
    }),
    singleValue: (styles) => ({
      ...styles,
      display: "flex",
      alignItems: "center",
    }),
    valueContainer: (styles) => ({
      ...styles,
      padding: "0",
      margin: "0",
      gap: "4px",
    }),
    control: (styles, { menuIsOpen }) => {
      return {
        ...styles,
        cursor: "pointer",
        borderWidth: 1,
        fontSize: "16px",
        display: "flex",
        boxShadow: "",
        borderRadius: "8px",
        padding: "9px 14px",
        "&:hover": {
          borderColor: GREY300,
        },
      };
    },
    dropdownIndicator: (styles, state) => ({
      ...styles,
      padding: "6px",
    }),
    option: (styles, { isSelected, isFocused }) => {
      return {
        ...styles,
        cursor: "pointer",
        padding: "10px 14px",
        color: GREY900,
        backgroundColor: "white",
        "&:hover": { backgroundColor: GREY50 },
      };
    },
    multiValue: (styles) => {
      return {
        ...styles,
        margin: "0",
        alignItems: "center",
        justifyContent: "center",
        gap: "4px",
        padding: "0px 4px 0px 3px",
        backgroundColor: "white",
        border: `1px solid ${GREY300}`,
        borderRadius: 6,
      };
    },
    multiValueLabel: (styles) => ({
      ...styles,
      padding: "0",
      color: GREY700,
      fontSize: "14px",
      fontWeight: 500,
      overflow: "hidden",
      textOverflow: "ellipsis",
      whiteSpace: "nowrap",
      maxWidth: "55px",
    }),
    multiValueRemove: (styles) => ({
      ...styles,
      width: 16,
      height: 16,
      padding: "0 0 0 1px",
      marginLeft: "0",
      color: GREY400,
    }),

    menuPortal: (base) => ({ ...base, zIndex: 9999 }),
  };
  return (
    <div
      data-cy="modal-add-priority-dropdown"
      style={{ position: "relative", maxWidth: 205, minWidth: 205 }}
    >
      <Select
        value={value}
        styles={Styles}
        maxMenuHeight={320}
        menuPortalTarget={document.body}
        onChange={handleChange}
        options={options}
        components={{
          IndicatorSeparator: () => null,
          Option: (props) => {
            if (props.options) {
              return (
                <components.Option {...props}>
                  <div data-cy={`modal-add-priority-item`} className="flex">
                    {props.data.label}
                  </div>
                </components.Option>
              );
            } else {
              return <></>;
            }
          },
        }}
      />
    </div>
  );
}
